.App {
	text-align: center;
	color: #fff;
	background: #111;
}

.is-desktop-device .App {
	min-height: 100vh;
}

.Datepicker {
    position: relative;
    margin: auto;
}

.is-mobile-device .Datepicker {
    max-width: 667px;
}

.is-desktop-device .Datepicker {
    display: inline-block;
    padding-top: 10px;
}

* {
	box-sizing: border-box;
}

